@import "../partials/variables";

//Theme 16
#atw_style16{
    .atw__title16{
        margin-bottom: 50px;
        padding-bottom: 0;
        h3{
            position: relative;
            &:before{
                position: absolute;
                content: "";
                width: 40%;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 1px;
                background: #e4e8ed;
            }
            &:after{
                position: absolute;
                content: "";
                width: 40%;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 1px;
                background: #e4e8ed;
            }
        }
        &::before{
            content: none;
        }
    }
    .atw--single_item{
        &:hover{
            .atw_item_top{
                .atw_overlay_content{
                    visibility: visible;
                    opacity: 1;
                    transform: scale(1);
                }
            }
        }
        .atw_item_top{
            position: relative;
            .badge--left{
                left: 0;
                top: 0;
                z-index: 1;
                .atw_badge--left{
                    border-radius: 0;
                    background: #000;
                }
            }
            .badge--right{
                right: 0;
                top: 0;
                z-index: 1;
                .atw_badge--right{
                    border-radius: 0;
                    background: #000;
                }
            }
            .atw_overlay_content{
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                background: rgba(0,0,0,0.3);
                visibility: hidden;
                opacity: 0;
                transform: scale(0.8);
                transition: $transition;
                .atw_product_actions{
                    li{
                        a{
                            display: block;
                            height: 45px;
                            padding: 0 12px;
                            border: 1px solid #e4e4ed;
                            background: #fff;
                            color: #000;
                            padding-top: 12px;
                        }
                    }
                }
            }
        }
        .atw_item_bottom{
            margin-top: 15px;
            .atw_item_header{
                display: flex;
                justify-content: flex-start;
                padding: 0;
                color:#9192a3;
                .atw_item_cat{
                    font-size: 14px;
                    margin-bottom: 0;
                    &+.atw_item_cat{
                        margin-left: 3px;
                    }
                }
                .atw_rating{
                    margin-left: auto;
                }
            }
            .atw_item_info{
                .atw_item_title{
                    padding: 10px 0 12px 0;
                    a{
                        font-size: 18px;
                        color: #000;
                        font-weight: 400;
                    }
                }
                .atw_price{
                    color: #9192a3;
                    del{
                        margin-right: 10px;
                        display: inline-block;
                        color: #000;
                    }
                    .atw_discount_ratio{
                        display: inline-block;
                        margin-left: 10px;
                        color: #000;
                    }
                }
            }
        }
    }
}